@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
body {
  font-family: 'Roboto', sans-serif;
  background-color: #eceff1;
}
.show-data {
  width: 100%;
  background-color: #cfd8dc;
  margin: 20px;
  padding: 10px;
  border-radius: 6px;
  box-shadow: 10px 10px 5px gray;

}
.show-info {
  width: 100%;
}
.show-info h1 {
  text-align: center;
}
.show-data img{
  display: block;
}
@media (min-width: 800px) {
    .show-data {
      display: flex;
    }
    .show-info {
      padding: 10px;
    }
}
@media (max-width: 800px) {
  img {
    margin:auto;
  }
  .show-info p {
    text-align: center;
    padding: 10px;
  }
}